.header {
	height: 80px;
	padding-top: 19px;
	padding-bottom: 19px;
	border-bottom: 1px solid #5c5c5e;
}

.nav {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
}

.menu {
	text-transform: uppercase;
	display: flex;
	gap: 20px;
	align-items: center;
}

.logo {
	justify-self: center;
}

.basket {
	position: relative;
	display: flex;
	align-items: center;
	justify-self: end;
}

.basket--on span {
	display: flex;
	position: absolute;
	bottom: 4px;
	right: -2px;
	font-size: 11px;
	width: 15px;
	height: 15px;
	background: var(--primary-gradient);
	border-radius: 50%;
	color: var(--white-color);
	line-height: 0;
	align-items: center;
	justify-content: center;
}

.section-title {
	text-transform: uppercase;
	font-weight: 400;
	font-size: 16px;
}

.hero {
	min-height: calc(100vh - 81px);
	background-image: url(../images/hero-bg.jpg);
	background-repeat: no-repeat;
	background-size: cover;
	background-position: bottom center;
}

.hero__title {
	font-weight: 400;
	font-size: 96px;
	line-height: 1.14;
	padding-top: 230px;
}

.hero__subtitle {
	font-size: 36px;
	line-height: 1.14;
	margin-bottom: 5px;
}
.hero__text {
font-size: 13px;
line-height: 1.5;
margin-bottom: 50px;
}

.hero__buy-text {
	font-size: 13px;
	line-height: 1.5;
	padding-top: 6px;
}

.buy-button {
	font-family: 'Helvetica', sans-serif;
	font-weight: 700;
	cursor: pointer;
	border: none;
	background: var(--primary-gradient);
	display: flex;
	padding: 0;
	text-transform: uppercase;
	padding: 1px;
}

.buy-button__text {
	color: var(--white-text-color);
	padding: 20px 110px;
	text-transform: uppercase;
}

.buy-button__price {
	color: var(--primary-text-color);
	background-color: var(--secondary-button-bg-color);
	padding: 20px 103px;
}

.product-features {
	padding: 100px 0;
}

.product-features__titles {
	display: flex;
}

.product-features__title {
	flex-basis: 300px;
	padding-right: 10px;
}

.product-features__slogan {
	max-width: 330px;
	font-size: 36px;
	line-height: 1.15;
	text-transform: uppercase;
	background: var(--primary-gradient);
		background-clip: text;
		-webkit-text-fill-color: transparent;
	margin-bottom: 2px;
}

.content-container {
	display: grid;
	grid-template-columns: 300px auto;
}

.product-features__items {
	display: flex;
	flex-direction: column;
	align-items: start;
}

.product-features__item-button {
	font-weight: 700;
	margin-bottom: 20px;
}

.product-features__item-button--active {
	color: var(--activ-text-color);
}

.product-features__content {
	position: relative;
	min-height: 320px;
}

.product-features__item-content {
	position: absolute;
	display: grid;
	grid-template-columns: auto 280px;
	grid-template-rows: auto 150px;
	grid-gap: 40px;
	opacity: 0;
	transition: opacity .5s ease; 
}

.product-features__item-content--active {
	opacity: 1;
}

.product-features__image {
	grid-row: 1/-1;
	align-self: stretch;
}

.product-features__item-box h3 {
	font-weight: 400;
	font-size: 24px;
	margin-bottom: 5px;
}

.video {
	grid-column: 2/3;
	width: 280px;
	height: 150px;
	background-image: url(../images/product-features2.jpg);
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	display: flex;
	flex-direction: column;
	justify-content: end;
	align-items: flex-start;
	padding: 9px 10px;
}

.video__button {
	margin-left: 22px;
}

.video span {
	font-size: 13px;
	color: var(--white-text-color);
}

.games {
	margin-bottom: 100px;
}

.games__title {
	margin-bottom: 50px;
}

.games__inner {
	display: grid;
	grid-template-columns: 1fr 3fr;
}

.games__descr {
	padding-right: 10px;
}

.games__slogan {
	font-size: 36px;
	line-height: 1.15;
	text-transform: uppercase;
	background: var(--primary-gradient);
		background-clip: text;
		-webkit-text-fill-color: transparent;
	margin-bottom: 10px;
}

.games__text {
	margin-bottom: 30px;
}

.games__descr-link {
	display: inline-block;
	font-weight: 700;
	font-size: 16px;
	text-transform: uppercase;
	background: var(--primary-gradient);
		background-clip: text;
		-webkit-text-fill-color: transparent;
	padding: 16px 47px;
	border: 1px solid var(--accent-color);
}

.games__list {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
}

.games__item {
	border: 1px solid;
	border-image: linear-gradient(180deg, #BC10D8 0%, rgba(80, 0, 250, 0.93) 100%) 1;
	padding: 20px;
	display: flex;
	flex-direction: column;
}

.games__img {
	margin-bottom: 10px;
}

.games__name {
	font-style: 24px;
	margin-bottom: 10px;
}

.games__text {
	margin-bottom: 30px;
}

.games__link {
	background: var(--primary-gradient);
		background-clip: text;
		-webkit-text-fill-color: transparent;
	margin-top: auto;
}

.promo {
	max-width: 1800px;
	margin: 0 auto 110px;
	text-align: center;
	position: relative;
	overflow-x: hidden;
}

.promo__bg {
	position: absolute;
	background: var(--primary-gradient);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	background-clip: text;
	// text-fill-color: transparent;
	text-transform: uppercase;
	font-weight: 700;
	font-size: 360px;
	line-height: 0.9;
	opacity: 0.1;
	z-index: -1;
	animation: line-text 7s linear 1s infinite;
}

@keyframes line-text {
	from {
		transform: translateX(120%);
		transform-origin: left center;
	}
	to {
		transform: translateX(-120%);
		transform-origin: right center;
	}
}

.promo__image {
	display: inline-block;
	margin-bottom: 30px;
}

.promo .buy-button {
	margin: 1px auto;
}

.complete-set {
	margin-bottom: 100px;
}

.complete-set__title {
	margin-bottom: 50px;
}

.complete-set__list {
	display: grid;
	grid-column-gap: 25px;
	grid-template-columns: 2fr 1fr 1fr;
	grid-template-rows: repeat(2, 1fr);
}

.complete-set__item--large {
	grid-row: 1/-1;
}

.complete-set__img {
	margin-bottom: 10px;
	object-fit: cover;
}

.complete-set__subtitle {
	margin-bottom: 10px;
}

.complete-set__slogan {
	display: block;
  width: 280px;
  height: 220px;
	font-size: 36px;
	line-height: 1.15;
	text-transform: uppercase;
	background: var(--primary-gradient);
		background-clip: text;
		-webkit-text-fill-color: transparent;
	margin-bottom: 10px;
}

.metaverse {
	margin-bottom: 100px;
}

.metaverse__box{
	position: relative;
	background-image: url(../images/metaverse.jpg);
	min-height: 500px;
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center;
	text-align: center;
}

.metaverse__button {
	position: absolute;
	bottom: 80px;
	left: 0;
	right: 0;
	margin: 0 auto;
	font-weight: 700;
	color: var(--white-color);
	background: var(--primary-gradient);
	border-radius: 50%;
	width: 50px;
	height: 50px;
	display: flex;
	justify-content: center;
	align-items: center;
}

.accessories {
	margin-bottom: 100px;
}

.accessories__title {
	margin-bottom: 30px;
}

.accessories__slogan {
	font-size: 36px;
	line-height: 1.15;
	text-transform: uppercase;
	background: var(--primary-gradient);
		background-clip: text;
		-webkit-text-fill-color: transparent;
	margin-bottom: 30px;
}

.accessories__list {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
}

.accessories__item {
	padding: 20px;
	border: 1px solid;
	border-image: linear-gradient(180deg, #bc10d8 0%, rgba(80, 0, 250, 0.93) 100%) 1;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.accessories__img {
	margin-bottom: 10px;
}

.accessories__subtitle {
	font-size: 24px;
	color: var(--activ-text-color);
	min-height: 67px;
}

.accessories__text {
	margin-top: auto;
	margin-bottom: 35px;
}

.accessories__price {
	font-weight: 700;
	line-height: 1.5;
	color: var(--activ-text-color);
	margin-top: auto;
	margin-bottom: 20px;
}

.accessories__button {
	font-weight: 700;
	line-height: 1.5;
	border: 1px solid;
	border-image: linear-gradient(180deg, #bc10d8 0%, rgba(80, 0, 250, 0.93) 100%) 1;
	padding: 6px;
	text-transform: uppercase;
	background: var(--primary-gradient);
		background-clip: text;
		-webkit-text-fill-color: transparent;
}

.feedback {
	margin-bottom: 100px;
}

.feedback__subtitle {
	font-size: 24px;
	line-height: 1.4;
	color: var(--activ-text-color);
	margin-bottom: 10px;
}

.feedback__inner {
	display: flex;
	justify-content: space-between;
	align-items: end;
}

.feedback__form {
	flex-basis: 600px;
}

.form {
	margin-bottom: 10px;
	position: relative;
}

.form__input {
	width: 100%;
	border: none;
	border-bottom: 1px solid #cacad6;
	padding: 22px 70px 22px 0;
}

.form__input::placeholder {
	font-family: 'Helvetica';
	font-style: normal;
	font-weight: 400;
	font-size: 13px;
	line-height: 1.15;
}

.form__button {
	padding: 0;
	width: 50px;
	height: 50px;
	background-color: var(--form-color);
	text-transform: uppercase;
	font-weight: 700;
	color: var(--primary-text-color);
	border-radius: 50%;
	position: absolute;
	right: 0;
	bottom: 5px;
}